.boton-detail{
  position: relative;
  padding: 1em 1.5em;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  font-size: 18px;
  margin: 1em 0.8em;

}
.boton-detail.type1 {
    color: #ffffff;
  }
  .boton-detail.type1.type1::after, .boton-detail.type1.type1::before {
    content: "";
    display: block;
    position: absolute;
    width: 20%;
    height: 20%;
    border: 2px solid;
    transition: all 0.6s ease;
    border-radius: 2px;
  }
  .boton-detail.type1.type1::after {
    bottom: 0;
    right: 0;
    border-top-color: transparent;
    border-left-color: transparent;
    border-bottom-color: #ffffff;
    border-right-color: #ffffff;
  }
  .boton-detail.type1.type1::before {
    top: 0;
    left: 0;
    border-bottom-color: transparent;
    border-right-color: transparent;
    border-top-color: #ffffff;
    border-left-color: #ffffff;
  }
  .boton-detail.type1.type1:hover:after, .boton-detail.type1.type1:hover:before {
    width: 100%;
    height: 100%;
  }


.boton-detail{
    cursor: pointer;
}
